nav {
    height: 8vh;
    width: 100vw;
    background-image: lightseagreen;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    top: 0;
    left: 0;
    z-index: 3;
}

.nav_container {
    height: 7vh;
    width: 90vw;
    margin: 0 auto;
    background-image: lightseagreen;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav_title_wrapper {
    display: flex;
    align-items: center;
    margin-top: 20px;
}

.logo {
    width: 40px;
    height:40px;
    border-radius: 50%;
    aspect-ratio: 1/1;
    margin-right: 2vw;
}

.title{
    color:blue;
    font-size: 25px;
    margin-left: -20px;
    font-weight: 600;
    margin-top: 25px;
}